header {
  position: relative;
  height: 100vh;
  background-size: cover;
  background-image: url(data:image/jpeg;base64,);
}
header .header_content {
  position: absolute;
  display: flow-root;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 750px;
  height: 50%;
  text-align: center;
  font-family: Exo, sans-serif;
}
